import "./section05.scss";
import React, { useCallback, useEffect, useRef, useState } from "react";
import process_ico from "../../../asset/images/process_icon.png";
import { Fade } from "react-reveal";
import { useInView } from "react-intersection-observer";
import { contents } from "./ContentSetting";

export default function Section05() {
  const { ref, inView } = useInView(); // viewRef
  const liRef = useRef(null); // liTagRef
  const [progressW, setProgressW] = useState(); // 진행도 css 상태변수
  const [progressStep, setProgressStep] = useState(1); // 진행도 상태변수

  // width, progres 상태 초기화
  const progressWidthHandler = () => {
    setProgressStep(1);
    clearInterval(progressIntervalHandler);
    if (liRef.current) {
      const progressClientRect = liRef.current.getBoundingClientRect();
      const Distance = progressClientRect.width;
      setProgressW(progressClientRect.x + Distance / 2);
      return { Distance };
    }
  };

  const progressIntervalHandler = () => {
    const { Distance } = progressWidthHandler();
    setInterval(() => {
      setProgressW((prev) => prev + Distance);
      setProgressStep((prev) => prev + 1);
    }, 3500);
  };

  useEffect(() => {
    if (inView) {
      clearInterval(progressIntervalHandler);
      progressIntervalHandler();
    } else {
      setProgressStep(1);
    }
  }, [inView]); // inView 상태 바뀔때마다 이펙트 발생

  useEffect(() => {
    if (progressStep > 10) {
      progressWidthHandler();
    }
    window.addEventListener("resize", progressWidthHandler);
  }, [progressStep]);

  return (
    <div className="section05-container">
      <div ref={ref} style={{ height: "50px" }}></div>
      {inView && <img src={process_ico} />}

      <Fade bottom duration={1200}>
        <h3 className="section05-head-txt">
          Development
          <br />
          Process
        </h3>
      </Fade>
      <Fade bottom duration={1500}>
        <p className="section05-sub-txt">제작 및 납품 절차</p>
      </Fade>

      {document.body.clientWidth > 1440 && (
        <div className="section05-progress-container">
          <div className="section05-progress-progress-bar"></div>
          <div
            className="section05-progress-progress-bar-complete"
            style={{ width: progressW + "px" }}
          ></div>
        </div>
      )}

      <div className="section05-process-txt">
        <ul className="section05-process-list">
          {document.body.clientWidth > 1440 ? (
            contents.map((content) => (
              <li key={content.id} ref={content.id === 1 ? liRef : null}>
                <b>{content.id}</b>
                <h4>{content.title}</h4>
                <p>
                  {content.content.map((v) => (
                    <div>
                      <span>{v.title1}</span>
                      <span>{v.title2}</span>
                      <span>{v.title3}</span>
                    </div>
                  ))}
                </p>
              </li>
            ))
          ) : (
            <div style={{}}>
              <li ref={liRef}></li>
              {contents
                .filter((content) => content.id === progressStep)
                .map((filterElement) => (
                  <li
                    style={{
                      width: "100%",
                    }}
                  >
                    <h2
                      style={{
                        wordBreak: "keep-all",
                        color: "#ffffff",
                        width: "400px",
                        margin: "0 auto",
                      }}
                    >
                      {filterElement.id}.{filterElement.title}
                    </h2>
                    <img
                      style={{
                        animation: "none",
                        display: "block",
                        margin: "30px auto 0 auto",
                      }}
                      src={filterElement.processImageUrl}
                    />
                  </li>
                ))}
            </div>
          )}
        </ul>
      </div>
    </div>
  );
}
