export default function BackgroundProvider(props, { bgSrc, width, height }) {
  console.log(props.bgSrc);
  return (
    <div
      style={
        props.bgSrc.includes(["png", "jpg", "jpeg"])
          ? {
              background: `url(${props.bgSrc}) center center`,
              width,
              height: props.height,
              overflow: "hidden",
            }
          : {
              background: props.bgSrc,
              width,
              height: props.height,
              overflow: "hidden",
            }
      }
    >
      <div
        style={{
          width: "80vw",
          height: "100%",
          margin: "0 auto",
          maxWidth: "1320px",
          minWidth: "325px",
        }}
      >
        {props.children}
      </div>
    </div>
  );
}

BackgroundProvider.defaultProps = {
  bgSrc: "#ffffff",
  width: "100vw",
  height: "100vh",
};
